import React from 'react';

import {TextGroup, Important, Title, SubTitle, Line} from '../../components/TextUtils/TextUtils.js';

const version = "3.1.2";

const UpdateTitle = () => {
    return "TwitchLink " + version;
};

const UpdateNote = () => {
    return (
        <div>
            <SubTitle>업데이트 노트</SubTitle>
            <div style={{padding: "30px 10px"}}>
                <TextGroup>
                    <Title>버그 수정 및 개선</Title>
                    <Line/>
                    임시 폴더가 간헐적으로 삭제되지 않는 오류를 수정하였습니다.
                    <br/>
                    콘텐츠 접근에 대한 인증이 만료되는 경우 다운로드가 올바르게 중단되지 않는 문제를 해결하였습니다. (주로 생방송 다운로드를 24시간 이상 진행할 시 발생)
                    <br/>
                    일부 버그가 수정되었습니다.
                    <br/>
                    일부 기능이 개선되었습니다.
                </TextGroup>
            </div>
        </div>
    );
};

const SourceUrl = "https://github.com/devhotteok/TwitchLink/releases/tag/" + version;

export default {
    UpdateTitle,
    UpdateNote,
    SourceUrl
}