import React from "react";

import "./FooterArea.css";

const FooterArea = (props) => {
  const DEVELOPER = "DevHotteok";
  const FIRST_RELEASE_YEAR = 2021;
  const thisYear = new Date().getFullYear();
  const year = Math.max(thisYear, FIRST_RELEASE_YEAR);
  return (
    <footer className="FooterArea">
      <div>&copy; {year} {DEVELOPER}.</div>
      {
        props.language === "ko"
        ?
        null
        :
        <div className="contact">
          <div>
            Email: twitchlinkhelp@gmail.com
          </div>
        </div>
      }
    </footer>
  );
};

export default FooterArea;