import React from 'react';

import {TextGroup, Important, Title, SubTitle, Line} from '../../components/TextUtils/TextUtils.js';

const version = "3.0.3";

const UpdateTitle = () => {
    return "TwitchLink " + version;
};

const UpdateNote = () => {
    return (
        <div>
            <SubTitle>Update Note</SubTitle>
            <div style={{padding: "30px 10px"}}>
                <TextGroup>
                    <Title>Bug Fixes and Improvements</Title>
                    <Line/>
                    Fixed an issue that caused the app to crash intermittently when the network was unstable.
                    <br/>
                    The download will not be interrupted even if there is a connection error.
                    <br/>
                    Some bugs were fixed.
                    <br/>
                    Some features were improved.
                </TextGroup>
            </div>
        </div>
    );
};

const SourceUrl = "https://github.com/devhotteok/TwitchLink/releases/tag/" + version;

export default {
    UpdateTitle,
    UpdateNote,
    SourceUrl
}