import React from 'react';

import {TextGroup, Important, Title, SubTitle, Line} from '../../components/TextUtils/TextUtils.js';

const version = "3.1.2";

const UpdateTitle = () => {
    return "TwitchLink " + version;
};

const UpdateNote = () => {
    return (
        <div>
            <SubTitle>Update Note</SubTitle>
            <div style={{padding: "30px 10px"}}>
                <TextGroup>
                    <Title>Bug Fixes and Improvements</Title>
                    <Line/>
                    Addressed an intermittent issue where the temporary folder was not being deleted.
                    <br/>
                    Resolved an issue where downloads wouldn't correctly stop when stream authentication expired. (This issue commonly arises when downloading live broadcasts for more than 24 hours.)
                    <br/>
                    Some bugs were fixed.
                    <br/>
                    Some features were improved.
                </TextGroup>
            </div>
        </div>
    );
};

const SourceUrl = "https://github.com/devhotteok/TwitchLink/releases/tag/" + version;

export default {
    UpdateTitle,
    UpdateNote,
    SourceUrl
}