import React from 'react';

import {TextGroup, Important, Title, SubTitle, Line} from '../../components/TextUtils/TextUtils.js';

const version = "3.0.3";

const UpdateTitle = () => {
    return "TwitchLink " + version;
};

const UpdateNote = () => {
    return (
        <div>
            <SubTitle>업데이트 노트</SubTitle>
            <div style={{padding: "30px 10px"}}>
                <TextGroup>
                    <Title>버그 수정 및 개선</Title>
                    <Line/>
                    네트워크가 불안정할 때 간헐적으로 앱이 충돌하는 문제를 수정하였습니다.
                    <br/>
                    연결 오류가 발생하여도 다운로드를 중단하지 않습니다.
                    <br/>
                    일부 버그가 수정되었습니다.
                    <br/>
                    일부 기능이 개선되었습니다.
                </TextGroup>
            </div>
        </div>
    );
};

const SourceUrl = "https://github.com/devhotteok/TwitchLink/releases/tag/" + version;

export default {
    UpdateTitle,
    UpdateNote,
    SourceUrl
}