import React from 'react';

import {TextGroup, Important, Title, SubTitle, Line} from '../../components/TextUtils/TextUtils.js';

const version = "3.2.0";

const UpdateTitle = () => {
    return "TwitchLink " + version;
};

const UpdateNote = () => {
    return (
        <div>
            <SubTitle>업데이트 노트</SubTitle>
            <div style={{padding: "30px 10px"}}>
                <TextGroup>
                    <Title>새로운 기능 및 개선</Title>
                    <Line/>
                    <Important>이제 다크 모드를 사용할 수 있습니다.</Important>
                    <br/>
                    파일명 생성 규칙에 'UNIX 시간' 변수가 추가되었습니다.
                    <br/>
                    일부 버그가 수정되었습니다.
                    <br/>
                    일부 기능이 개선되었습니다.
                </TextGroup>
            </div>
        </div>
    );
};

const SourceUrl = "https://github.com/devhotteok/TwitchLink/releases/tag/" + version;

export default {
    UpdateTitle,
    UpdateNote,
    SourceUrl
}